<template>
  <b-container fluid id="app">
    <!-- Pop-up met tussentijdse scores -->
    <intermediate-result-modal v-if="start & !hideScores & !hideIntermediateResults" @showRelevanteFactorenClick="showRelevanteFactorenClick()" :effecten="effecten" @closeTussentijdse="hideScores = true" :progressDonePercentage="donePercentage"
      :displayEffecten="displayEffecten" :totalScores="totalScores" :totalScoresCategories="totalScoresCategories">
    </intermediate-result-modal>

    <!-- Intro tekst -->
    <b-container fluid class="wrapper">
      <b-card class="introCard">
        <b-alert show>Copyright TNO in opdracht van het Ministerie van Financiën. Het betreft hier een prototype dat gepubliceerd is in december 2022. Dit prototype dient voor test & evaluatie doeleinden: aan de uitkomsten van de huidige versie van dit instrument
          kunnen dan ook geen conclusies worden verbonden.
        </b-alert>
        <h1 class="display-3 title">Kwetsbaarheidsanalyse personele veiligheid</h1>
      </b-card>
    </b-container>

    <!-- Tabel met geanalyseerde functies -->
    <b-container fluid class="wrapper">
      <b-card class="functieAnalyseCard">
        <h1 class="display-4 sectionTitle my-0 py-0">Opgeslagen functies</h1>
        <FunctieTabel :done-percentage="donePercentage"></FunctieTabel>
      </b-card>
    </b-container>

    <!-- Analyseer een nieuwe functie -->
    <b-container fluid class="wrapper">
      <b-card class="functieAnalyseCard">
        <h1 class="display-4 sectionTitle">Analyseer een nieuwe functie</h1>
        <p class="explanation">Analyseer een nieuwe functie door alle factoren in het onderstaande formulier in te
          vullen. Door op een rij te klikken open je de lijst aan mogelijke variabelen per factor. Wanneer je klaar bent
          met invullen kan je de functie een naam geven en deze toevoegen aan de overzichtstabel.</p>
        
        <!-- Controls -->
        <b-row class="analyseControls">
          <b-col cols="2">
            <b-button variant="danger" @click="emptyAllFields()">Leeg alle velden</b-button>
          </b-col>
          <b-col cols="2">
            <b-button variant="primary" @click="save()">Sla functie op</b-button>
          </b-col>
          <b-col cols="4">
            <b-form-input class="functieNaamInput" v-model="functieNaam" placeholder="Functie naam"></b-form-input>
          </b-col>
          <b-col cols="4" class="d-flex align-items-center text-align-center">
            <b-form-checkbox v-model="hideIntermediateResults">&nbsp;Verberg tussentijdse resultaten (pop-up)</b-form-checkbox>
          </b-col>
        </b-row>

        <!-- Vragenlijst. Volgt de hierarchische structuur Determinant (thema) > Indicator (groep) > Factor (vraag) > Variabele (antwoord) -->
        <b-row class="tool">
          <b-col cols="12">
            <!-- Loop over determinanten -->
            <b-row v-for="(det_obj, det_key) in hierarchy" :key="det_key">
              <p class="determinant"> {{ det_key }} </p>
              <b-col cols="12">
                <!-- Loop over indicatoren -->
                <b-row v-for="(ind_obj, ind_key) in det_obj" :key="ind_key">
                  <b-col cols="3" class="indicator">
                    {{ ind_key }}
                    <b-icon-info-circle-fill
                      class="info" v-if="hasInfoIndicators[ind_key]"
                      @click="infoModalIndicators(indicator_factor_beschrijvingen[ind_key]['beschrijving'])">
                    </b-icon-info-circle-fill>
                  </b-col>
                  <b-col cols="9">
                    <!-- Loop over factoren -->
                    <b-row v-for="(fact_obj, fact_key) in ind_obj" :key="fact_key"
                      :ref="makeIndicatorFactorString(ind_key, fact_key)">
                      <b-col class="factor">
                        <b-row v-b-toggle="`collapse-${makeIndicatorFactorString(ind_key, fact_key)}`"
                          :style="getFilledColor(currentVarChoices[makeIndicatorFactorString(ind_key, fact_key)])"
                          class="factorName" :key="keyCounter">
                          <b-col>
                            ▼ {{ capitaliseString(fact_key) }} {{ multiChoiceString(makeIndicatorFactorString(ind_key,
                            fact_key)) }}

                            <b-icon-info-circle-fill
                              @click="infoModal(ind_key, fact_key, beschrijving[makeIndicatorFactorString(ind_key, fact_key)])"
                              class="info" v-if="hasInfo[makeIndicatorFactorString(ind_key, fact_key)]">
                            </b-icon-info-circle-fill>
                          </b-col>
                        </b-row>
                        <b-collapse :id="`collapse-${makeIndicatorFactorString(ind_key, fact_key)}`"
                          accordion="my-accordion" style="background-color: white">
                          <b-form-group>
                            <!-- Loop over variabelen -->
                            <b-form-checkbox v-for="variable in fact_obj"
                              :disabled="checkMultiChoiceDisabled(makeIndicatorFactorString(ind_key, fact_key), variable)"
                              :key="variable" @change="computeScores(); keyCounter += 1; hideScores = false"
                              v-model="currentVarChoices[makeIndicatorFactorString(ind_key, fact_key)]"
                              name="checkboxes" :value="variable" :text-field="variable">&nbsp;{{ capitaliseString(variable) }}
                            </b-form-checkbox>
                          </b-form-group>
                        </b-collapse>
                      </b-col>
                    </b-row>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
          
        </b-row>

        <!-- Repeat controls -->
        <b-row class="analyseControls">
          <b-col cols="2">
            <b-button variant="danger" @click="emptyAllFields()">Leeg alle velden</b-button>
          </b-col>
          <b-col cols="2">
            <b-button variant="primary" @click="save()">Sla functie op</b-button>
          </b-col>
          <b-col cols="4">
            <b-form-input class="functieNaamInput" v-model="functieNaam" placeholder="Functie naam"></b-form-input>
          </b-col>
          <b-col cols="4" class="d-flex align-items-center text-align-center">
            <b-form-checkbox v-model="hideIntermediateResults">&nbsp;Verberg tussentijdse resultaten (pop-up)</b-form-checkbox>
          </b-col>
        </b-row>
      </b-card>
    </b-container>

    <!-- Modal met de relavante factoren per effect -->
    <b-modal size="lg" id="relevante-factoren-modal" ref="relevante-factoren-modal">
      <RelevanteFactorenModal :effecten="effecten" :progressDonePercentage="donePercentage" :totalBrutoNetto="totalBrutoNetto"
        :displayEffecten="displayEffecten" :totalScores="totalScores" :totalScoresOpbouw="totalScoresOpbouw"
        :totalScoresCategories="totalScoresCategories"></RelevanteFactorenModal>
    </b-modal>

    <!-- Modal met extra info over een factor en de opties -->
    <b-modal id="my-info-modal" ref="my-info-modal" hide-footer>
      <b-row v-if="infoBeschrijving['factor_beschrijving'] != '-'">
        <b-col cols="12">
          <b>{{ infoBeschrijving['factor_beschrijving'] }}</b>
        </b-col>
      </b-row>
      <br v-if="infoBeschrijving['factor_beschrijving'] != '-'"/>
      <b-row>
        <b-col cols="12" v-for="(value, key) in infoBeschrijving['per_variable']" :key="key">
          <b>{{ strHelpReverse(key) }}</b> {{ value }}
        </b-col>
      </b-row>
    </b-modal>

    <b-modal id="my-info-modal-indicator" ref="my-info-modal-indicator" hide-footer>
      <b-row>
        <b-col cols="12">
          {{ infoBeschrijvingIndicator }}
        </b-col>
      </b-row>
    </b-modal>

    <!-- Modal met warning dat niet alle factoren zijn ingevuld (als dat het geval is en je klikt op opslaan) -->
    <b-modal id="not-all-filled-warning-modal" ref="not-all-filled-warning-modal" hide-footer>
      Niet alle factoren zijn gedefinieerd, weet je zeker dat je de functie will toe voegen aan de tabel?
      <b-row>
        <b-col>
          <b-button class="mt-3" variant="outline-danger" block
            @click="saveStateAndRestart(); $bvModal.hide('not-all-filled-warning-modal')">Negeer oningevulde factoren</b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-button class="mt-3" block @click="$bvModal.hide('not-all-filled-warning-modal')">Ga door met invullen</b-button>
        </b-col>
      </b-row>
    </b-modal>

  </b-container>
</template>

<script>
import { centralStore } from '@/stores/main'
import IntermediateResultModal from './components/IntermediateResultModal.vue'
import FunctieTabel from './components/FunctieTabel'
import RelevanteFactorenModal from './components/RelevanteFactorenModal'
import json from './assets/ind_factor_weights_and_hierarchy.json'

export default {
  name: 'App',
  components: { FunctieTabel, IntermediateResultModal, RelevanteFactorenModal },
  data() {
    return {
      hierarchy: undefined,
      start: false,
      weights: undefined,
      beschrijving: undefined,
      infoBeschrijvingIndicator: undefined,
      keyCounter: 0, // to trigger re-render
      currentVarChoices: {}, // v-model for the chosen variables per indicator-factor
      donePercentage: 0.0,
      max: 100.0,
      store: centralStore(),
      hideScores: false,
      hideIntermediateResults: false,
      infoBeschrijving: "",
      functieNaam: "",
      hasInfo: {},
      hasInfoIndicators: {},
      hasInfoFactors: {},
      displayEffecten: [
        'agressieve-bejegening',
        'doelgerichte-benadering',
        'corruptie,-omkoping,-fraude-gevoeligheid',
        'psychosociale-overbelasting'
      ],
      totalScores: {
        'agressieve-bejegening': 1.0,
        'doelgerichte-benadering': 1.0,
        'corruptie,-omkoping,-fraude-gevoeligheid': 1.0,
        'psychosociale-overbelasting': 1.0,
        'overall': 1.0,
      },
      totalBrutoNetto: {},
      totalScoresCategories: {
        'agressieve-bejegening': "Neutraal",
        'doelgerichte-benadering': "Neutraal",
        'corruptie,-omkoping,-fraude-gevoeligheid': "Neutraal",
        'psychosociale-overbelasting': "Neutraal",
        'overall': "Neutraal",
      },
      overAllMultiplier: 1.0,
      totalScoresOpbouw: {
        'agressieve-bejegening': [],
        'doelgerichte-benadering': [],
        'corruptie,-omkoping,-fraude-gevoeligheid': [],
        'psychosociale-overbelasting': [],
        'overall': [],
      },
      effecten: {
        'agressieve-bejegening': 'Agressieve bejegening',
        'doelgerichte-benadering': 'Doelgerichte benadering',
        'corruptie,-omkoping,-fraude-gevoeligheid': 'Corruptie-, omkoping- en fraudegevoeligheid',
        'psychosociale-overbelasting': 'Psychologische overbelasting',
        'overall': "Overall vermenigvuldigingsfactor",
      },
      multipleChoiceIndicatorFactors: [
        "risico's--product",
        "risico's--risico-gebied",
        "aard-werkzaamheden--taakeisen",
        "aard-werkzaamheden--agressie-/-geweld",
        "positie--regelruimte",
        "leiderschap--gericht-op:",
        "leiderschap--functionele-steun",
        "cultuur--sociale-steun",
        "cultuur--psychologische-veiligheid",
        "psychosociale-factoren--mogelijke-stressoren",
      ],
    }
  },
  mounted() {
    // Laad de coeeficienten & hierarchie van determinanten, indicatoren en factoren
    // Deze structuur bepaald hoe de structuur van de invul lijst eruit ziet
    this.hierarchy = json.hierarchy;
    this.weights = json.weights;
    this.beschrijving = json.beschrijving;
    this.indicator_factor_beschrijvingen = json.indicator_factor_beschrijvingen;
    this.initCurrentChoices();
    this.initHasInfo();
    this.initHasInfoIndicatorFactors();
    this.computeBrutoNettoCumulatief();
  },
  methods: {
    showRelevanteFactorenClick() {
      this.$bvModal.show('relevante-factoren-modal')
    },
    infoModalIndicators(beschrijving) {
      this.infoBeschrijvingIndicator = beschrijving;
      this.$refs["my-info-modal-indicator"].show();
    },
    infoModal(indicator, factor, beschrijving) {
      this.infoBeschrijving = {
        "factor_beschrijving": this.indicator_factor_beschrijvingen[indicator]["factoren"][factor]["beschrijving"],
        "per_variable": beschrijving};
      this.$refs["my-info-modal"].show();
    },
    emptyAllFields() {
      this.initCurrentChoices();
      this.computeScores();
      this.functieNaam = "";
      this.start = false;
    },
    computeBrutoNettoCumulatief() {
      // Naast de multiplicatieve score wordt er een cumulatieve score bijgehouden
      // deze score leent zich beter voor een bruto - netto afwezig
      let brutoNetto = {};

      let totalVersterkend = 0;
      let totalNVersterkend = 0;
      let totalBeschermend = 0;
      let totalNBeschermend = 0;
      
      for (const [effect, opbouw] of Object.entries(this.totalScoresOpbouw)) {
        let versterkend = 0;
        let nVersterkend = 0;
        let beschermend = 0;
        let nBeschermend = 0;
        for (const weight of opbouw) {
          let factorWeight = weight[2]
          // weight = [indFactor, value, factorWeight] 
          if (factorWeight > 1.0) {
            versterkend += factorWeight;
            nVersterkend += 1;
          // For negative take the inverse (for example, 0.5 becomes 2.0)
          // Otherwise negative and positive are not comparable
          } else if (factorWeight < 1.0) {
            beschermend += (1.0 / factorWeight);
            nBeschermend += 1.0;
          }
        }

        totalVersterkend += versterkend;
        totalNVersterkend += nVersterkend;

        totalBeschermend += beschermend;
        totalNBeschermend += nBeschermend;

        brutoNetto[effect] = {
          versterkend: versterkend, // bruto
          nVersterkend: nVersterkend,
          beschermend: beschermend,
          nBeschermend: nBeschermend,
          netto: versterkend - beschermend, // netto
          nNetto: nVersterkend - nBeschermend,
        }
      }
      brutoNetto["total"] = {
        versterkend: totalVersterkend, // bruto
        nVersterkend: totalNVersterkend,
        beschermend: totalBeschermend,
        nBeschermend: totalNBeschermend,
        netto: totalVersterkend - totalBeschermend, // netto
        nNetto: totalNVersterkend - totalNBeschermend,
      }

      this.totalBrutoNetto = brutoNetto;
    },
    getDonePercentage() {
      // How many of all the questions you have filled in so far
      let N = Object.keys(this.currentVarChoices).length;
      let counter = 0;
      for (const value of Object.values(this.currentVarChoices)) {
        if (value.length > 0) {
          counter += 1;
        }
      }
      this.donePercentage = (counter / N) * 100;
    },
    reasonOverallScore() {
      // Dringend aandacht nodig; aandacht vereist; aandacht gewenst; geen bijzonderheden
      if (this.overallScore <= 1.00) {
        return "Geen bijzonderheden";
      } else if (this.overallScore <= 3.0) {
        return "Aandacht gewenst";
      } else if (this.overallScore <= 5.0) {
        return "Aandacht vereist";
      } else {
        return "Dringend aandacht nodig";
      }
    },
    initHasInfoIndicatorFactors() {
      // Determines for all indicators & variables whether it has an information entry 
      // (to determine whether to display a circle with an "i" in it)
      
      for (const [indicator, indicatorObj] of Object.entries(this.indicator_factor_beschrijvingen)) {
        
        if (indicatorObj["beschrijving"] != "-") {
          this.hasInfoIndicators[indicator] = true;
        } else {
          this.hasInfoIndicators[indicator] = false;
        }

        for (const [factor, factorObject] of Object.entries(indicatorObj["factoren"])) {
          if (factorObject["beschrijving"] != "-") {
            this.hasInfoFactors[factor] = true;
          } else {
            this.hasInfoFactors[factor] = false;
          }
        }
      }

    },
    initHasInfo() {
      // Determines for all variables whether it has an information entry 
      // (to determine whether to display a circle with an "i" in it)
      let hasDescription;
      for (const [indFactor, variableValues] of Object.entries(this.beschrijving)) {
        hasDescription = false;
        for (const beschrijving of Object.values(variableValues)) {
          if (beschrijving != "-") {
            hasDescription = true;
          }
        }
        this.hasInfo[indFactor] = hasDescription;
      }
    },
    save() {
      // Check if all is filled in.
      let allFilled = true;
      for (const v of Object.values(this.currentVarChoices)) {
        if (v.length == 0) {
          allFilled = false;
        }
      }

      // If filled, save automatically, else first show a warning modal
      if (allFilled) {
        this.saveStateAndRestart();
      } else {
        this.$refs['not-all-filled-warning-modal'].show();
      }
    },
    logResults() {
      // Log de resultaten in de console voor quick check
      console.log("*************************")
      console.log("Functie:", this.functieNaam)
      console.log("Scores opbouw:")
      let counter = 0;
      for (const [key, value] of Object.entries(this.totalScoresOpbouw)) {
        console.log(counter, key)
        for (const variableScore of value) {
          console.log(variableScore[0], variableScore[1], variableScore[2])
        }
        console.log("----------")
        counter += 1;
      }
      console.log("Subtotale scores:")
      for (const [key, value] of Object.entries(this.totalScoresOpbouw)) {
        if (key != "overall") {
          let computeString = `${key} =`;
          for (const variableScore of value) {
            computeString += `${variableScore[2]} * `;
          }
          computeString = computeString.substring(0, computeString.length - 2)
          computeString += `= ${this.totalScores[key].toFixed(4)}`

          console.log(computeString)
          console.log("----------")
        }
      }
      console.log(`Totale score = sqrt( ${this.totalScores['agressieve-bejegening'].toFixed(3)} * ${this.totalScores['corruptie,-omkoping,-fraude-gevoeligheid'].toFixed(3)} * ${this.totalScores['doelgerichte-benadering'].toFixed(3)}  * ${this.totalScores['psychosociale-overbelasting']} * [overall] ${this.totalScores['overall'].toFixed(3)}) = ${this.overallScore.toFixed(3)}`)
      console.log("*************************")
    },
    saveStateAndRestart() {
      // Save by pushing the results to the store
      // this.logResults() // for debug
      this.store.$patch((state) => {
        state.functies.push({
          'functieNaam': this.functieNaam,
          'currentVarChoices': { ...this.currentVarChoices },
          'totalScores': { ...this.totalScores },
          'totalBrutoNetto': { ...this.totalBrutoNetto },
          'totalScoresOpbouw': { ...this.totalScoresOpbouw },
          'totalScoresCategories': { ...this.totalScoresCategories },
          'overallScore': this.overallScore,
          'conclusion': this.reasonOverallScore(this.overallScore),
        });
      })
      this.initCurrentChoices();
      this.computeScores();
      this.functieNaam = "";
      this.start = false;
    },
    capitaliseString(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    multiChoiceString(indicatorFactorKey) {
      // Add a message that you can select multiple options if applicable
      if (this.multipleChoiceIndicatorFactors.includes(indicatorFactorKey)) {
        return " (selecteer opties die van toepassing zijn, mogelijk meerdere of geen)";
      } else {
        return "";
      }
    },
    checkMultiChoiceDisabled(indicatorFactorKey, variable) {
      // Deze functie checkt welke opties nog aangevinkt mogen worden. Als er bijvoorbeeld "onbekend" is ingevuld, 
      // mogen alle andere opties automatisch niet meer ingevuld worden.
      let selectedArray = this.currentVarChoices[indicatorFactorKey];
      let selectedArrayTrimLower = selectedArray.map(element => { return element.toLowerCase().trim(); });

      const multiOptions = ["geen bijzonderheden", "geen van bovenstaande", "onbekend", "niet van toepassing", "ambigue", "anders"];

      // Check of een van deze "speciale" opties in de geselecteerde opties zit
      let nonMultiOptionFound = false;
      let selectedMultiOption = "";
      for (const option of multiOptions) {
        if (selectedArrayTrimLower.includes(option)) {
          nonMultiOptionFound = true;
          selectedMultiOption = option;
        }
      }

      // Als er een "speciale" variabele is geselecteerd en de variabele is niet de variabele in kwestie: disable!
      // VB: als "onbekend" is aangevinkt, dan moeten alle andere opties op disabled worden gezet
      if ((nonMultiOptionFound ) && (variable.toLowerCase().trim() != selectedMultiOption)) {
        return true;
      
      // Als er een "niet-speciale" variabele is geselecteerd (en er is wel minimaal 1 optie geselecteerd) 
      // en de variabele is van het speciale soort: disable!
      // VB: als er een normale optie is geselecteerd, kan er niet tegelijkertijd "onbekend" 
      // of "geen" meer worden geselecteerd.
      } else if ((!nonMultiOptionFound) && (selectedArray.length > 0) && (multiOptions.includes(variable.toLowerCase().trim()))) {
        return true;
      }

      // Als het een multiple choice variable betreft
      if (this.multipleChoiceIndicatorFactors.includes(indicatorFactorKey)) {
        if ((nonMultiOptionFound) && (!multiOptions.includes(variable.toLowerCase().trim()))) {
          return true;
        } else {
          return false;
        }
      } else {
        if (!selectedArray.includes(variable) && selectedArray.length > 0) {
          return true;
        } else {
          return false;
        }
      }
    },
    computeScores() {
      // Totale score = Sqrt [ ABscore *DBscore *COscore *POscore * OverallScore] 

      this.start = true;
      this.getDonePercentage();
      let total = 1.0;
      let s;
      for (const effect in this.totalScores) {
        s = this.computeAssignScore(effect);
        total = total * s;
      }
      this.overallScore = Math.sqrt(total);

      this.computeBrutoNettoCumulatief();

      // console.log(`Overall score (met multiplier van ${this.totalScores["overall"]})`, total);
      // console.log("Sqrt score", this.overallScore);
    },
    categoriseScore(score) {
      // Map score to a ordinal category so we don't have to show numbers, but just in words
      let scoreCategory;

      if (score <= 1.0) {
        scoreCategory = "Laag";
      } else if (score <= 3.0) {
        scoreCategory = "Gemiddeld";
      } else if (score <= 5.0) {
        scoreCategory = "Hoog";
      } else {
        scoreCategory = "Zeer hoog";
      }

      return scoreCategory;
    },
    computeScore(effect) {
      // Score per effect is alle coefficienten behoordend bij aangevinkte factoren keer elkaar
      // Wanneer de scores niet 1.0 zijn hebben ze effect, anders zijn ze "neutraal"

      let score = 1.0;
      let opbouw = [];

      for (const [indFactor, valueArray] of Object.entries(this.currentVarChoices)) {
        if (valueArray != []) {
          for (const value of valueArray) {
            // Look up the coefficient
            let factorWeight = this.weights[indFactor][this.strHelp(value)][effect];

            score = score * factorWeight;

            // If the score is not neutral, save the factor to determine which factors contributed to a certain score
            if (factorWeight != 1.0) {
              let s = [indFactor, value, factorWeight];
              opbouw.push(s);
            }
          }
        }
      }

      let scoreCategory = this.categoriseScore(score);

      return [opbouw, score, scoreCategory];
    },
    computeAssignScore(effect) {
      // Compute the score and assign to internal state
      let [opbouw, score, scoreCategory] = this.computeScore(effect)
      this.totalScoresOpbouw[effect] = opbouw;
      this.totalScores[effect] = score;
      this.totalScoresCategories[effect] = scoreCategory;
      return score;
    },
    makeFactVarString(fact, variable) {
      let factVar = `${fact}--${variable}`;
      factVar = this.strHelp(factVar);
      return factVar;
    },
    getFilledColor(array) {
      // If we have filled in a question, mark it with light grey so we can easily spot which ones
      // are done and which ones are not.
      let style;
      if (array.length > 0) {
        style = "background-color: rgba(135, 161, 174, 0.3)";
      } else {
        style = "background-color: white";
      }
      return style;
    },
    strHelp(s) {
      return s.replaceAll(" ", "-").toLowerCase()
    },
    strHelpReverse(s) {
      return this.capitaliseString(s.replaceAll("-", " "));
    },
    makeIndicatorFactorString(indKey, factKey) {
      let indicatorFactorString = `${indKey}--${factKey}`;
      indicatorFactorString = this.strHelp(indicatorFactorString);
      return indicatorFactorString;
    },
    initCurrentChoices() {
      for (const property in this.weights) {
        this.currentVarChoices[property] = [];
      }
    }
  },
} 
</script>

