<template>
  <b-container v-if="render" class="relevanteFactorenWrapper">
    <!-- PDF header -->
    <b-row v-if="forExport" :class="{'export-class': forExport, 'html2pdf__page-break': forExport, 'effectRow': true}" >
      <b-alert show>Copyright TNO in opdracht van het Ministerie van Financiën. Het betreft hier een prototype dat gepubliceerd is in september 2022. Dit prototype dient voor test & evaluatie doeleinden: aan de uitkomsten van de huidige versie van dit instrument
        kunnen dan ook geen conclusies worden verbonden.
      </b-alert>
      <h1 class="display-3 title">Kwetsbaarheidsanalyse personele veiligheid</h1>
      <h3 class="display-4 subtitle">Resultaten analyse functie {{ functieNaam }}</h3>
    </b-row>
    
    <!-- Bruto / netto analyse resultaten (gebaseerd op cumulatieve scores) -->
    <b-row v-if="totalBrutoNetto != null"  :class="{'export-class': forExport, 'html2pdf__page-break': forExport}">
      <b-col cols="12">
        <h1 class="h1-title">Analyse inherente kwetsbaarheid</h1>
        <p class="bruto-netto-explanation"><em>Los van een score per kwetsbaarheidsdomein, kennen we ook een score toe aan de inherente kwetsbaarheid van een functie en aan de beschermende factoren. Inherente kwetsbaarheid houdt in dat sec naar de versterkende factoren wordt gekeken en er geen mitigerende factoren worden meegewogen.</em></p>
      </b-col>
      <b-col :cols="forExport ? 12 : 5">
        <p class="inherente-kwetsbaarheid-conclusie">De inherente kwetsbaarheid van deze functie wordt geclassificeerd als: <b>{{ classifyInherenteKwetsbaarheid(this.totalBrutoNetto["total"]["versterkend"])}}</b>.</p>
        <p class="beschermende-factoren-conclusie">De beschermende factoren worden geclassificeerd als: <b>{{ classifyBeschermendeFactoren(this.totalBrutoNetto["total"]["beschermend"])}}</b>.</p>
      </b-col>
      <!-- don't render in PDF -->
      <b-col v-if="!forExport" cols="7">
        <Bar
          id="bruto-netto-bar-chart"
          :options="chartOptions"
          :data="chartData"
        />
      </b-col>
    </b-row>
    
    <!-- Analyse resultaten per kwetsbaarheid domein -->
    <b-row>
      <b-col>
        <h1 class="h1-title">Analyse per kwetsbaarheidsdomein</h1>
      </b-col>
    </b-row>

    <!-- Loop over domeinen -->
    <b-row :class="{'export-class': forExport, 'html2pdf__page-break': forExport, 'effectRow': true}" v-for="(effectKey, index) in displayEffecten" :key="effectKey"  >
      <b-col>
        <p class="effectName"><em>{{ effecten[effectKey] }}</em></p>
        <b-row>
          <b-col cols="4" class="gauge-wrapper">
            <vue-svg-gauge
              :start-angle="-110"
              :end-angle="110"
              :value="totalScores[effectKey]"
              :separator-step="0"
              :min="0"
              :max="8"
              :inner-radius="60"
              :gauge-color="gaugeColor"
              :scale-interval="1">
            </vue-svg-gauge>
            <div class="gauge-text">
                {{ totalScoresCategories[effectKey] }}
            </div>
          </b-col>

          <b-col cols="8">
            <h3 class="negatief-positief-header">Versterkende factoren</h3>
            <b-row v-if="negatieveFactoren[effectKey].length > 0" class="reason">
              <div v-for="(reason, index) in negatieveFactoren[effectKey]" :key="`${effectKey}-${index}`">
                <b-icon-patch-plus-fill class="plus" v-if="reason[2] > 1.0"></b-icon-patch-plus-fill> 
                <b-icon-patch-minus-fill class="minus" v-else></b-icon-patch-minus-fill> 
                &nbsp;<b>{{ translateFactorString(reason[0]) }}:</b> {{ reason[1] }}
              </div>
            </b-row>
            <b-row v-else class="reason">
              <div> - </div>
            </b-row>

            <h3 class="negatief-positief-header">Mitigerende factoren</h3>
            <b-row class="reason" v-if="positieveFactoren[effectKey].length > 0" >
              <div v-for="(reason, index) in positieveFactoren[effectKey]" :key="`${effectKey}-${index}`">
                <b-icon-patch-plus-fill class="plus" v-if="reason[2] > 1.0"></b-icon-patch-plus-fill> 
                <b-icon-patch-minus-fill class="minus" v-else></b-icon-patch-minus-fill> 
                &nbsp;<b>{{ translateFactorString(reason[0]) }}:</b> {{ reason[1] }}
              </div>
            </b-row>
             <b-row v-else class="reason">
              <div> - </div>
            </b-row>
          </b-col>
        </b-row>
        <!-- Lines in the PDF between the blocks -->
        <hr v-if="(index < 3) && forExport">
      </b-col>
    </b-row>
  </b-container>

</template>

<script>
import { VueSvgGauge } from 'vue-svg-gauge'
import { Bar } from 'vue-chartjs'
ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js';
import {Chart} from 'chart.js';
import ChartDataLabels from "chartjs-plugin-datalabels";
Chart.register(ChartDataLabels);

export default {
    components: {
      VueSvgGauge, Bar
    },
    props: {
      effecten: Object,
      displayEffecten: Array,
      totalScores: Object,
      totalScoresOpbouw: Object,
      totalScoresCategories: Object,
      totalBrutoNetto: Object,
      forExport: Boolean,
      functieNaam: String,
    },
    data() {
      return {
        render: false,
        
        chartOptions: {
          responsive: true,
          scales: {
            y: {
              ticks: {
                display: false,
              },
              display: false,
              // min: 0,
              // max: 10,
            },
            x: {
              grid: {
                display: false,
              },
            }
          },
          plugins: {
            legend: {
              display: false
            },
            tooltip: {
              enabled: false,
            },
            datalabels: {
              formatter: this.classifyValue,
              color: "black",
              textAlign: "end",
              font: {
                weight: "light",
                size: 10
              }
            }
          }
        },
        gaugeColor: [
          {offset: 0,  color: '#B3FF78'},
          {offset: 12.5,  color: '#B3FF78'},
          {offset: 42, color: '#F2EE78'},
          {offset: 70, color: '#FF8441'},
          {offset: 100, color: '#FF0F00'},
        ],
      }
    },
    mounted() {
      // Only go about rendering when actual data is passed to the object
      if (Object.keys(this.totalScores).length > 0) {
        this.render = true;
        this.negatieveFactoren = this.getNegatieveFactoren();
        this.positieveFactoren = this.getPositieveFactoren();
      }
    },  
    computed: {
      chartData() { 
        return {
          labels: [ 'Inherente kwetsbaarheid', 'Beschermende factoren'],
          datasets: [ { 
            data: [
              this.totalBrutoNetto['total']['versterkend'], 
              this.totalBrutoNetto['total']['beschermend']],
            yAxisID: "y",
            xAxisID: "x",
            barThickness: 40,
            backgroundColor: [
              "rgba(255, 15, 0, 0.8)", // red
              "rgba(138, 222, 73, 0.8)" // green
            ] } ]
        }
      }
    },
    methods: {
      classifyValue(value, context) {
        let classification = ""
        if (context.dataIndex == 0) {
          classification = this.classifyInherenteKwetsbaarheid(value);
        } else {
          classification = this.classifyBeschermendeFactoren(value);
        }
        return classification.charAt(0).toUpperCase() + classification.slice(1);
      },
      classifyInherenteKwetsbaarheid(value) {
        // Deze functie moet ook worden aangepast in component FunctieTabel!
        if (value <= 38.) {
          return `niet kwetsbaar`;
        } else if (value <= 46.) {
          return `lichtelijk kwetsbaar`;
        } else if (value <= 52.) {
          return `kwetsbaar`;
        } else {
          return `zeer kwetsbaar`;
        }
      },
      classifyBeschermendeFactoren(value) {
        // Deze functie moet ook worden aangepast in component FunctieTabel!
        if (value <= 35.) {
          return `onvoldoende aanwezig`;
        } else if (value <= 47.) {
          return `aanwezig`;
        } else if (value <= 49) {
          return `nadrukkelijk aanwezig`;
        } else {
          return `zeer nadrukkelijk aanwezig`;
        }
      },
      getNegatieveFactoren() {
        // Filter alle coeeficienten op negatief bijdragende factoren (< 1.0) en sorteer ze.
        let negatieveFactorenAlleEffecten = {};
        for (const [effect, opbouw] of Object.entries(this.totalScoresOpbouw)) {
          let negatieveFactoren = []
          for (const factorScoreArray of opbouw) {
            if (factorScoreArray[2] > 1.0) {
              negatieveFactoren.push(factorScoreArray)
            }
          }
          negatieveFactoren.sort((a, b) => {return b[2] - a[2]});
          negatieveFactorenAlleEffecten[effect] = negatieveFactoren;
        }
        return negatieveFactorenAlleEffecten;
      },
      getPositieveFactoren() {
        // Filter alle coeeficienten op positief bijdragende factoren (> 1.0) en sorteer ze.
        let positieveFactorenAlleEffecten = {};
        for (const [effect, opbouw] of Object.entries(this.totalScoresOpbouw)) {
          let positieveFactoren = []
          for (const factorScoreArray of opbouw) {
            if (factorScoreArray[2] < 1.0) {
              positieveFactoren.push(factorScoreArray)
            }
          }
          positieveFactoren.sort((a, b) => {return a[2] - b[2]});
          positieveFactorenAlleEffecten[effect] = positieveFactoren;
        }
        return positieveFactorenAlleEffecten;
      },
      capitaliseString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
      },
      translateFactorString(string) {
        return this.capitaliseString(string.replace("--", " | ").replace("-", " "));
      },
    },
  }
  
  </script>

